import axios from "axios";

// Service exposing the urls for the API
import * as ApiUrls from "../ApiUrlService";

/**
 * Services for the applicant module of the eipo application
 */
export const InitiateNewApplication = applicationData => {
  return axios({
    method: "POST",
    url: ApiUrls.EipoInitiateApplication,
    data: applicationData
  });
};

export const SaveApplication = applicationData => {
  return axios({
    method: "POST",
    url: ApiUrls.EipoSaveApplication,
    data: applicationData
  });
};

export const SubmitApplication = applicationData => {
  return axios({
    method: "POST",
    url: ApiUrls.EipoSubmitApplicationURL,
    data: applicationData
  });
};

export const AuthenticateExistingApplication = credentials => {
  // return new Promise((resolve, reject) => {
  //   setTimeout(() => {
  //     resolve({ success: true, data: "hgwgeytw6" });
  //   }, 2000);
  // });

  return axios({
    method: "post",
    url: ApiUrls.EipoAuthenticateExistingApplication,
    data: credentials
  });
};

export const GetStockbrokers = () => {
  return axios({
    method: "get",
    url: ApiUrls.EipoGetStockbrokers
  });
};



