/**
 * This service file handles calling of services, and making store mutations with their responses.
 * I honestly don't know where best to put these kinda actions, so this is a stop gap solution begging
 * for refactoring from a smarter mind, or future me :)
 *
 */

import * as ApplicationService from "./ApplicantService";
import * as mutationTypes from "../../store/mutation-types";
import store from "../../store";

/**
 * Get the list of stockbrokers from the API, and commit it to the store.
 */
export const addStockbrokers = () => {
  ApplicationService.GetStockbrokers().then(stockbrokers => {
    let brokers = stockbrokers.data.data ? stockbrokers.data.data : [];

    brokers = brokers.map(broker => {
      return {
        text: broker.name,
        value: broker.id
      };
    });

    store.commit(mutationTypes.STORE_STOCKBROKERS, brokers);
  });
};



