import Vue from "vue";
import Router from "vue-router";
import Login from "@/components/Login";
// import Dashboard from "@/components/Dashboard";
import PageNotFound from "@/components/404";

Vue.use(Router);

/**
 * Components for E-IPO APPLICANT MODULE
 */
import LandingPage from "@/components/e_ipo/applicant/LandingPage";
import IpoApplication from "@/components/e_ipo/applicant/NewIpoApplication";

export default new Router({
  routes: [
    {
      path: "/public",
      redirect: ""
    },
    {
      path: "/",
      name: "landingPage",
      component: LandingPage
    },
    {
      path: "/home",
      name: "landingPage",
      component: LandingPage
    },
    {
      path: "/home",
      name: "landingPage",
      component: LandingPage
    },
    {
      path: "/apply/:formSegment",
      name: "applyPageWithFormSegment",
      component: IpoApplication,
      props: true
    },
    {
      path: "/apply",
      name: "applyPage",
      component: IpoApplication
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    // {
    //   path: "/home",
    //   name: "Home",
    //   component: Dashboard
    // },
    {
      path: "*",
      name: "PageNotFound",
      component: PageNotFound
    }
  ]
});



