<template>
    <v-container  fluid class="pt0 d-flex" justify-center>
      <v-layout row class="xs12 lg8 wrapper--summary--form">
          <h3 class="bg-csp-blue text-white mb0 w100p pt5 pb5 pl10"> APPLICATION SUMMARY </h3>
          <table class="table" id="applicationSummaryTable">
              <tr>
                  <td class="w50p"><b>Application Type</b></td>
                  <td class="w50p">{{ getApplicationTypeText() | uppercase }}</td>
              </tr>
               <tr>
                  <td><b>Email Address</b></td>
                  <td>{{ applicationInitiationData.email  | uppercase}}</td>
              </tr>
               <!-- <tr>
                  <td><b>Phone Number</b></td>
                  <td>{{ applicantData.address_street }}</td>
              </tr> -->
               <tr v-if="isCorporateApplication()" class="bg-csp-light-blue text-white">
                  <td><b>COMPANY DETAILS</b></td>
                  <td></td>
              </tr>
                <tr v-if="isCorporateApplication()">
                  <td><b>Company Name</b></td>
                  <td>{{ applicationInitiationData.companyName | uppercase}}</td>
                </tr>
                <tr v-if="isCorporateApplication()">
                  <td><b>Contact Person</b></td>
                  <td>{{ applicationInitiationData.contactPerson | uppercase}}</td>
               </tr>
                <tr v-if="isIndividualApplication()">
                  <td><b>Surname</b></td>
                  <td>{{ applicationInitiationData.lastName | uppercase}}</td>
               </tr>
               <tr v-if="isIndividualApplication()">
                  <td><b>First Name</b></td>
                  <td>{{ applicationInitiationData.firstName | uppercase}}</td>
               </tr>
               <tr v-if="isIndividualApplication()">
                  <td><b>Other Names</b></td>
                  <td>{{ applicantData.otherName | uppercase}}</td>
               </tr>
                <tr v-if="isJointApplication()">
                  <td><b>Surname (1)</b></td>
                  <td>{{ applicationInitiationData.lastName1 | uppercase}}</td>
               </tr>
               <tr v-if="isJointApplication()">
                  <td><b>First Name (1)</b></td>
                  <td>{{ applicationInitiationData.firstName1 | uppercase}}</td>
               </tr>
               <tr v-if="isJointApplication()">
                  <td><b>Other Names (1)</b></td>
                  <td>{{ applicationInitiationData.otherName1 | uppercase}}</td>
               </tr>
                 <tr v-if="isJointApplication()">
                  <td><b>Surname (2)</b></td>
                  <td>{{ applicationInitiationData.lastName2 | uppercase}}</td>
               </tr>
               <tr v-if="isJointApplication()">
                  <td><b>First Name (2)</b></td>
                  <td>{{ applicationInitiationData.firstName2 | uppercase}}</td>
               </tr>
               <tr v-if="isJointApplication()">
                  <td><b>Other Names (2)</b></td>
                  <td>{{ applicationInitiationData.otherName2 | uppercase}}</td>
               </tr>
               <tr v-if="!isCorporateApplication()">
                  <td><b>Next Of Kin (Name)</b></td>
                  <td>{{ applicantData.nok_name | uppercase}}</td>
              </tr>
              <tr v-if="!isCorporateApplication()">
                  <td><b>Next Of Kin (Phone Number)</b></td>
                  <td>{{ applicantData.nok_phoneNumber | uppercase}}</td>
              </tr>
              <tr class="bg-csp-light-blue text-white">
                  <td><b>ADDRESS</b></td>
                  <td></td>
              </tr>
               <tr>
                  <td><b>Street</b></td>
                  <td>{{ applicantData.address_street | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>City</b></td>
                  <td>{{ applicantData.address_city | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>State</b></td>
                  <td>{{ applicantData.address_state | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>Phone Number</b></td>
                  <td>{{ applicantData.phoneNumber }}</td>
              </tr>
              <tr class="bg-csp-light-blue text-white">
                  <td><b>INVESTMENT & ALLOTMENT DETAILS</b></td>
                  <td></td>
                  
              </tr>
              <tr class="w100p bg-dark-gray">
                  <td><b>BID (1)</b></td>
                  <td class="bg-dark-gray"></td>
              </tr>
              <tr>
                  <td>
                      <b>NO OF SHARES:</b> {{ applicantData.noOfShares_1 | currency('',0) }}
                  </td>
                  <td>
                      <div class="w49p d-inline-block"><b>BID PRICE:</b> {{ applicantData.bidPrice_1 | currency('',2) }}</div>
                      <div class="w49p d-inline-block"><b>AMOUNT:</b> {{ applicantData.amount_1 | currency('',2)}}</div>
                  </td>
              </tr>
               <tr class="w100p bg-dark-gray">
                  <td><b>BID (2)</b></td>
                  <td class="bg-dark-gray"></td>
              </tr>
              <tr>
                  <td>
                      <b>NO OF SHARES:</b> {{ applicantData.noOfShares_2 | currency('',0) }}
                  </td>
                  <td>
                      <div class="w49p d-inline-block"><b>BID PRICE:</b> {{ applicantData.bidPrice_2 | currency('',2) }}</div>
                      <div class="w49p d-inline-block"><b>AMOUNT:</b> {{ applicantData.amount_2 | currency('',2)}}</div>
                  </td>
              </tr>
              <tr class="w100p bg-dark-gray">
                  <td><b>BID (3)</b></td>
                  <td class="bg-dark-gray"></td>
              </tr>
              <tr>
                  <td>
                      <b>NO OF SHARES:</b> {{ applicantData.noOfShares_3 | currency('',0) }}
                  </td>
                  <td>
                      <div class="w49p d-inline-block"><b>BID PRICE:</b> {{ applicantData.bidPrice_3 | currency('',2) }}</div>
                      <div class="w49p d-inline-block"><b>AMOUNT:</b> {{ applicantData.amount_3 | currency('',2)}}</div>
                  </td>
              </tr>
              
               <tr>
                  <td><b>Investor's CSCS Account No</b></td>
                  <td>{{ applicantData.cscsNo | uppercase}}</td>
              </tr>
              <tr>
                  <td><b>Clearing House Number (CHN)</b></td>
                  <td>{{ applicantData.chnNo | uppercase}}</td>
              </tr>
              <tr>
                  <td><b>Stockbroker</b></td>
                  <td>{{ getStockbrokerText() | uppercase}}</td>
              </tr>
                <tr class="bg-csp-light-blue text-white">
                  <td><b>BANK DETAILS</b></td>
                  <td></td>
              </tr>
               <tr>
                  <td><b>Bank Name</b></td>
                  <td>{{ applicantData.bankName | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>Account Number</b></td>
                  <td>{{ applicantData.bankAccountNumber }}</td>
              </tr>
               <tr>
                  <td><b>Sort Code</b></td>
                  <td>{{ applicantData.bankSortCode }}</td>
              </tr>
               <tr>
                  <td><b>Bank City</b></td>
                  <td>{{ applicantData.bankCity | uppercase}}</td>
              </tr>
               <tr>
                  <td><b>Bank State</b></td>
                  <td>{{ applicantData.bankState | uppercase}}</td>
              </tr>

               <tr class="bg-csp-light-blue text-white">
                  <td><b>SIGNATURE(S)</b></td>
                  <td></td>
              </tr>

              <tr class="text-center">
                  <td> 
                      <canvas id="signature_1" width="100" height="100" 
                        style="background-color:lightgray;"></canvas>
                        <p>SIGNATURE ONE</p>
                  </td>
                  <td v-if="!isIndividualApplication()">
                      <canvas id="signature_2" width="100" height="100" 
                        style="background-color:lightgray;"></canvas>
                        <p>SIGNATURE TWO</p>
                  </td>
                  <td v-if="isIndividualApplication()"></td>
              </tr>
               
          </table>
      </v-layout>
  </v-container>
</template>


<script>
export default {
  data() {
    return {};
  },

  mounted() {
    // Draw signatures using the base64 string saved in the store
    const signatureOne = this.ipoApplicationState.signatures.signature_1;
    const signatureTwo = this.ipoApplicationState.signatures.signature_2;

    let ctx1 = document.querySelector("#signature_1").getContext("2d");
    let ctx2 = document.querySelector("#signature_2").getContext("2d");

    let img1 = new Image();
    let img2 = new Image();

    img1.onload = function() {
      ctx1.drawImage(img1, 0, 0, 100, 100);
    };
    img2.onload = function() {
      ctx2.drawImage(img2, 0, 0, 100, 100);
    };

    img1.src = signatureOne;
    img2.src = signatureTwo;
  },

  computed: {
    applicationInitiationData() {
      return this.$store.state.ipoApplication.applicationInitiationData;
    },
    applicantData() {
      return this.$store.state.ipoApplication.applicantData;
    },
    ipoApplicationState() {
      return this.$store.state.ipoApplication;
    }
  },

  methods: {
    isIndividualApplication() {
      return this.applicationInitiationData.applicationType === 1;
    },
    isJointApplication() {
      return this.applicationInitiationData.applicationType === 2;
    },
    isCorporateApplication() {
      return this.applicationInitiationData.applicationType === 3;
    },
    getApplicationTypeText() {
      switch (this.applicationInitiationData.applicationType) {
        case 1:
          return "Individual";
        case 2:
          return "Joint";
        case 3:
          return "Corporate";
        default:
          return "Individual";
      }
    },
    getStockbrokerText() {
      const brokerValue = this.applicantData.broker;
      const stockbroker = this.ipoApplicationState.stockbrokers.filter(
        broker => broker.value === brokerValue
      )[0];

      return stockbroker.text;
    }
  }
};
</script>


<style lang="sass" scoped>
    .wrapper--summary--form
        border: 2px solid #1a2155
    #applicationSummaryTable
        tr
            td:first-child
                text-transform: uppercase
</style>


