<template>
  <v-container fluid>
      <b class="text--red red--text font-size-13 mb40"> *Please note that only clear images are accepted </b>
      <v-layout row text-center class="mb50 mt40">
          <v-flex xs12 sm6>
             <file-uploader 
                    accept="image/*"
                    ref="fileInput"
                  @input="storeSignatureOne"
              ></file-uploader>
           </v-flex>

            <v-flex xs12 sm6>
               <file-uploader-two
              v-if="numberOfSignaturesNeeded() > 1"   
                    accept="image/*"
                    ref="fileInput"
                  @input="storeSignatureTwo"
              ></file-uploader-two>
            </v-flex>

      </v-layout>
  </v-container>
</template>


<script>
/**
 * Using 2 diff components, is a terrible hack which must be refactored, but sadly time is of the essence right now.
 * If found, please refactor :). This was done because the FileUploader component needs access to the "image"
 * data property of its parent, and no time to refactor it for multiple values.
 */
import FileUploader from "../../FileUploader";
import FileUploaderTwo from "../../FileUploader_2";

import * as mutationTypes from "../../../../store/mutation-types";

export default {
  components: {
    FileUploader,
    FileUploaderTwo
  },

  data() {
    return {
      image: "",
      image_2: "",
      base64Image: "",
      base64Image_2: "",
      title: "",
      description: ""
    };
  },

  methods: {
    storeSignatureOne(e) {
      const img = e;

      let reader = new FileReader();
      reader.addEventListener(
        "load",
        () => {
          this.image = reader.result;
          this.base64Image = reader.result;
          this.$store.commit(
            mutationTypes.UPDATE_SIGNATURE_ONE,
            this.base64Image
          );
        },
        false
      );

      if (img) {
        reader.readAsDataURL(img);
      }
    },
    storeSignatureTwo(e) {
      const img = e;

      let reader = new FileReader();
      reader.addEventListener(
        "load",
        () => {
          this.image_2 = reader.result;
          this.base64Image_2 = reader.result;
          this.$store.commit(
            mutationTypes.UPDATE_SIGNATURE_TWO,
            this.base64Image_2
          );
        },
        false
      );

      if (img) {
        reader.readAsDataURL(img);
      }
    },
    getImageFormData(image) {
      let formData = new FormData();
      formData.set("image", image);
      formData.set("title", this.title);
      formData.set("description", this.description);

      return formData;
    },

    isIndividualApplication() {
      return this.applicationInitiationData.applicationType === 1;
    },
    isJointApplication() {
      return this.applicationInitiationData.applicationType === 2;
    },
    isCorporateApplication() {
      return this.applicationInitiationData.applicationType === 3;
    },
    numberOfSignaturesNeeded() {
      switch (this.applicationInitiationData.applicationType) {
        case 1:
          return 1;
        case 2:
          return 2;
        case 3:
          return 2;
        default:
          return 1;
      }
    }
  },

  computed: {
    applicationInitiationData() {
      return this.$store.state.ipoApplication.applicationInitiationData;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>


