<script>
import * as mutationTypes from "../../../../store/mutation-types";
import { stockbrokers } from "../../../../data-mocks/e_ipo/applicant.mocks";
import _ from "lodash";

export default {
  data() {
    return {
      inPreviewState: false,
      requiredRule: [v => !!v || "Required"],
      stockbrokers: stockbrokers,
      applicantData: _.cloneDeep(
        this.$store.state.ipoApplication.applicantData
      ),
      ipoApplication: _.cloneDeep(this.$store.state.ipoApplication)
    };
  },

  computed: {
    applicationInitiationData() {
      return this.$store.state.ipoApplication.applicationInitiationData;
    },

    amount_1() {
      if (this.applicantData.noOfShares_1 && this.applicantData.bidPrice_1) {
        const amount =
          parseFloat(this.applicantData.noOfShares_1) *
          parseFloat(this.applicantData.bidPrice_1);

        this.applicantData.amount_1 = amount;
        return amount.toFixed(2);
      }
    },

    amount_2() {
      if (this.applicantData.noOfShares_2 && this.applicantData.bidPrice_2) {
        const amount =
          parseFloat(this.applicantData.noOfShares_2) *
          parseFloat(this.applicantData.bidPrice_2);

        this.applicantData.amount_2 = amount;
        return amount.toFixed(2);
      }
    },

    amount_3() {
      if (this.applicantData.noOfShares_3 && this.applicantData.bidPrice_3) {
        const amount =
          parseFloat(this.applicantData.noOfShares_3) *
          parseFloat(this.applicantData.bidPrice_3);

        this.applicantData.amount_3 = amount;
        return amount.toFixed(2);
      }
    }
  },

  watch: {
    applicantData: {
      handler: _.debounce(function(applicantData) {
        this.$store.commit(
          mutationTypes.UPDATE_APPLICANT_DATA_STATE,
          applicantData
        );
      }, 50),
      deep: true
    }
  },

  methods: {
    isIndividualApplication() {
      return this.applicationInitiationData.applicationType === 1;
    },
    isJointApplication() {
      return this.applicationInitiationData.applicationType === 2;
    },
    isCorporateApplication() {
      return this.applicationInitiationData.applicationType === 3;
    },
    calculateAmount(qty, price) {
      let amount = 0;
      try {
        amount = parseFloat(qty) * parseFloat(price);
      } catch (err) {
        amount = 0;
      }

      return amount;
    }
  }
};
</script>


