<template>
  <v-container fluid class="height-400px" justify-center >
    <v-layout row >
        <v-flex></v-flex>

        <!-- FORM INPUTS -->
        <v-flex xs12 sm10 lg6>
          <v-layout row justify-center class="d-flex">
              <!-- Application Type -->
              <v-flex xs12 class="height-55px mb6">
              <v-select class="" :label="'APPLICATION TYPE'" :items="applicationTypes" 
              v-model="applicationInitiationData.applicationType" v-validate="'required'" name="applicationInitiationData.applicationType" :rules="requiredRule"
              :disabled="applicationInitiationData.isInitiating">
              </v-select>
              </v-flex>

              <!-- Company Name -->
              <v-flex xs12 v-if="isCorporateApplication()" class="height-55px mb6">
                  <v-text-field   :label="'COMPANY NAME'" v-model="applicationInitiationData.companyName" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" 
                  name="applicationInitiationData.companyName" :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

               <!-- Company contact person Name -->
            <v-flex xs12 v-if="isCorporateApplication()" class="height-55px mb6">
                <v-text-field   :label="'CONTACT PERSON'" v-model="applicationInitiationData.contactPerson" 
                :type="'text'" :rules="requiredRule"  v-validate="'required'" 
                name="applicationInitiationData.contactPerson">
                </v-text-field>
            </v-flex>

              <!-- Email -->
              <v-flex xs12 class="height-55px mb6">
                  <v-text-field   :label="'EMAIL'" v-model="applicationInitiationData.email" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.email" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

              <!-- FirstName 1 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'FIRST NAME 1'" v-model="applicationInitiationData.firstName1" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.firstName1" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- LastName 1 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'LAST NAME 1'" v-model="applicationInitiationData.lastName1" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.lastName1" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- OtherName 1 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'OTHER NAMES 1'" v-model="applicationInitiationData.otherName1" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.otherName1" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- FirstName 2 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'FIRST NAME 2'" v-model="applicationInitiationData.firstName2" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.firstName2" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- LastName 2 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'LAST NAME 2'" v-model="applicationInitiationData.lastName2" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.lastName2" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- OtherName 2 -->
              <v-flex v-if="isJointApplication()" xs12 sm4 class="height-55px mb6">
                  <v-text-field   :label="'OTHER NAMES 2'" v-model="applicationInitiationData.otherName2" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.otherName2" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                  <!-- FirstName -->
              <v-flex v-if="isIndividualApplication()" xs12 sm6 class="height-55px mb6">
                  <v-text-field   :label="'FIRST NAME'" v-model="applicationInitiationData.firstName" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.firstName" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- LastName -->
              <v-flex v-if="isIndividualApplication()" xs12 sm6 class="height-55px mb6">
                  <v-text-field   :label="'LAST NAME'" v-model="applicationInitiationData.lastName" 
                  :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.lastName" 
                  :disabled="applicationInitiationData.isInitiating">
                  </v-text-field>
              </v-flex>

                <!-- OtherName -->
                <!-- <v-flex v-if="isIndividualApplication()" xs12 sm12 class="height-55px mb6">
                    <v-text-field   :label="'OTHER NAMES'" v-model="applicationInitiationData.otherName" 
                    :type="'text'" :rules="requiredRule"  v-validate="'required'" name="applicationInitiationData.otherName" 
                    :disabled="applicationInitiationData.inPreviewState">
                    </v-text-field>
                </v-flex> -->
              
          </v-layout>
        </v-flex>

        <v-flex></v-flex>
    </v-layout>
  </v-container>
</template>


<script>
import { applicationTypes } from "../../../../data-mocks/e_ipo/applicant.mocks";
import * as mutationTypes from "../../../../store/mutation-types";
import _ from "lodash";

export default {
  data() {
    return {
      applicationTypes,
      requiredRule: [v => !!v || "Required"],
      applicationInitiationData: _.cloneDeep(
        this.$store.state.ipoApplication.applicationInitiationData
      )
    };
  },

  watch: {
    applicationInitiationData: {
      handler: _.debounce(function(initiationData) {
        this.$store.commit(
          mutationTypes.UPDATE_APPLICATION_INITIATION_STATE,
          initiationData
        );
      }, 500),
      deep: true
    }
  },

  methods: {
    isIndividualApplication() {
      return this.applicationInitiationData.applicationType === 1;
    },
    isJointApplication() {
      return this.applicationInitiationData.applicationType === 2;
    },
    isCorporateApplication() {
      return this.applicationInitiationData.applicationType === 3;
    }
  }
};
</script>


<style lang="sass" scoped>

</style>



