<template>
  <v-container fluid>

    <!-- MultiStep form used in applying for IPO's -->
    <v-stepper v-model="applicantStepper">

      <!-- Application form headers -->
      <v-stepper-header>
        <v-stepper-step step="1" :complete="applicantStepper > 1">Initiate Application</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2" :complete="applicantStepper > 2">Applicant Data</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="3" :complete="applicantStepper > 3">Signature Upload</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="4" :complete="applicantStepper > 4">Summary</v-stepper-step>
         <v-divider></v-divider>
        <v-stepper-step step="5">Application Status</v-stepper-step>
      </v-stepper-header>
      <!-- EOF Application form headers -->

      <!-- Application Initiation -->
      <v-stepper-content step="1">

        <initiate-application></initiate-application>

        <!-- Loading Icon when Initiating Applications -->
        <v-layout v-if="initiatingApplication"  row class=" d-flex justify-center mt10">
          <v-flex class="xs6 d-flex justify-center align-center">
              <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
              <span class="font-size-12 ml5">Initiating your IPO application</span>
          </v-flex>
        </v-layout>
        
        <div class="w100p text-right">
            <v-btn error @click="goBack()" v-if="!applicationInitiated">Back</v-btn>
            <v-btn primary @click="initiateApplication()" v-if="!applicationInitiated">Initiate Application</v-btn>
           <v-btn primary @click.native="applicantStepper = 2" v-if="applicationInitiated">Continue Application</v-btn>
           <!-- <v-btn primary @click.native="applicantStepper = 2">Continue Application</v-btn> -->
        </div>

      </v-stepper-content>
      <!-- EOF Application initiation -->

      <!-- Applicant Data -->
      <v-stepper-content step="2">

        <div class="w100p text-right">
            <v-btn success 
              @click="saveApplication()"
              :loading="isSavingApplication"
              :disabled="isSavingApplication">
              Save
            </v-btn>
          <v-btn primary @click.native="applicantStepper = 3">Next</v-btn>
        </div>

        <applicant-data></applicant-data>

        <div class="w100p text-right">
            <v-btn success 
              @click="saveApplication()"
              :loading="isSavingApplication"
              :disabled="isSavingApplication">
              Save
            </v-btn>
            <v-btn primary @click.native="applicantStepper = 3">Next</v-btn>
        </div>

      </v-stepper-content>
      <!-- EOF Applicant Data -->

      <!-- Signature Upload -->
      <v-stepper-content step="3">

        <signature-upload></signature-upload>

        <div class="w100p text-right">
            <v-btn error @click.native="applicantStepper = 2">Previous</v-btn>
            <v-btn success 
              @click="saveApplication()"
              :loading="isSavingApplication"
              :disabled="isSavingApplication">
              Save
            </v-btn>
            <v-btn primary @click.native="applicantStepper = 4">Next</v-btn>
        </div>

      </v-stepper-content>
      <!-- EOF Signature Upload -->

      <!-- Application Summary -->
      <v-stepper-content step="4">

        <application-summary></application-summary>

        <!-- Loading Icon when submitting IPO's -->
        <v-layout v-if="submittingApplication"  row class=" d-flex justify-center mt10">
          <v-flex class="xs6 d-flex justify-center align-center">
              <v-progress-circular indeterminate class="primary--text height-20px width-20px"></v-progress-circular>
              <span class="font-size-12 ml5">Submitting your IPO application</span>
          </v-flex>
        </v-layout>

        <div class="w100p text-right">
            <v-btn error @click.native="applicantStepper = 3">Previous</v-btn>
            <v-btn success 
              @click="saveApplication()"
              :loading="isSavingApplication"
              :disabled="isSavingApplication">
              Save
            </v-btn>
          <v-btn primary @click="submitApplication()">Submit</v-btn>
        </div>

      </v-stepper-content>
      <!-- EOF Application Summary -->

      <!-- Application Status -->
      <v-stepper-content step="5">

        <application-status></application-status>

        <v-btn primary>Download PDF</v-btn>

      </v-stepper-content>
      <!-- EOF Application Status -->

    </v-stepper>
    <!-- EOF MultiStep form used in applying for IPO's -->

    <!-- Dialog for displaying application initiated message -->
    <v-dialog v-model="showApplicationInitiatedDialog" lazy absolute>

      <v-btn id="openApplicationInitiatedDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>

        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-20px">APPLICATION INITIATED</h5>
        </v-card-title>
        <!-- Form -->
        <v-card-text>
            <p>
              Your IPO application has been successfully initiated, and an email with your unique
              identification code has been sent to the registered email address. 
              Kindly keep this email, as the code is required for subsequent access to this application.
            </p>

            <!-- Buttons -->
            <v-container fluid class="p0 ">
              <v-layout  row class="mt20">
                <v-flex xs12 class="d-flex justify-center ">
                  <v-btn @click="closeApplicationInitiatedPopup()" small style="background: #4c7396; color: #FFFFFF"
                    >
                    CONTINUE APPLICATION
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
        </v-card-text>

      </v-card>
    </v-dialog>

     <!-- Dialog for displaying application submitted message -->
    <v-dialog v-model="showApplicationSubmittedDialog" lazy absolute>

      <v-btn id="openApplicationSubmittedDialog"
        style="display: none" color="primary" dark slot="activator">
      </v-btn>

      <v-card>
        <v-card-title class="d-flex justify-center bg-csp-light-blue p5 pt10">
          <h5 class="white--text font-size-20px">APPLICATION SUBMITTED</h5>
        </v-card-title>

        <v-card-text>
            <p>
              Your IPO application has been successfully submitted, and a document containing your application
              details has been sent to you. 
            </p>

            <!-- Buttons -->
            <v-container fluid class="p0 ">
              <v-layout  row class="mt20">
                <v-flex xs12 class="d-flex justify-center ">
                  <v-btn @click="closeApplicationSubmittedPopup()" small style="background: #4c7396; color: #FFFFFF"
                    >
                    OK
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>

        </v-card-text>
      </v-card>

    </v-dialog>

    <!-- Snackbar to show success message when user is added -->
    <v-snackbar success
      :timeout="snackbarTimeout"
      :top="true"
      :color="'success'"
      v-model="applicationSaved"
      >
      Application saved successfully
    </v-snackbar>

    <!-- Snackbar to show error message when user addition fails -->
    <v-snackbar error
      :timeout="snackbarTimeout"
      :top="true"
      :color="'error'"
      v-model="applicationSaveFailed"
      >
      Failed to save application
    </v-snackbar>

  </v-container>
</template>

<script>
/**
 * Components used to encapsulate each step of the IPO application phase.
 */
import InitiateApplication from "./forms/InitiateApplication";
import ApplicantData from "./forms/ApplicantData";
import SignatureUpload from "./forms/SignatureUpload";
import ApplicationSummary from "./forms/ApplicationSummary";
import ApplicationStatus from "./forms/ApplicationStatus";

/**
 * Access to HTTP API provided by HTTP Services
 */
import {
  SaveApplication,
  InitiateNewApplication,
  SubmitApplication
} from "../../../services/eipo/ApplicantService";

import * as mutationTypes from "../../../store/mutation-types";
import _ from "lodash";

export default {
  components: {
    InitiateApplication,
    ApplicantData,
    SignatureUpload,
    ApplicationSummary,
    ApplicationStatus
  },

  props: ["formSegment"],

  data() {
    return {
      snackbarTimeout: 4000,
      snackbarMode: "",
      applicantStepper: 0,
      applicationInitiationData: _.cloneDeep(
        this.$store.state.ipoApplication.applicationInitiationData
      ),
      applicationInitiated: false,
      initiatingApplication: false,
      submittingApplication: false,
      showApplicationInitiatedDialog: false,
      showApplicationSubmittedDialog: false,
      isSavingApplication: false,
      isSubmittingApplication: false,
      applicationSaved: false,
      applicationSaveFailed: false
    };
  },

  beforeMount() {
    // Move to a  form segment, if it was passed in as a param
    if (this.formSegment) {
      this.applicantStepper = this.formSegment;
    }
  },

  watch: {
    applicationInitiationData: {
      handler: _.debounce(function(initiationData) {
        this.$store.commit(
          mutationTypes.UPDATE_APPLICATION_INITIATION_STATE,
          initiationData
        );
      }, 50),
      deep: true
    }
  },

  methods: {
    /**
     * Leave the IPO application page, and go back to the landing page
     */
    goBack() {
      this.$router.push({ name: "landingPage" });
    },

    /**
     * Ensures that the initiation form is properly validated before the application is initiated
     *
     * @returns boolean
     */
    validApplicationInitiationForm() {
      const formData = this.applicationInitiationData;

      if (!formData.applicationType) {
        console.log("An application type is required");
        return false;
      }
    },

    /**
     * Initiate an IPO application
     */
    initiateApplication() {
      // Validate current form state before initiating an IPO Application
      // if (!this.validApplicationInitiationForm()) {
      //   return;
      // }

      this.initiatingApplication = true;
      this.$store.commit(mutationTypes.UPDATE_APPLICATION_INITIATION_STATE, {
        isInitiating: true
      });

      // Get current state of the application initiation state
      let applicationInitiationData = {
        ...this.$store.state.ipoApplication.applicationInitiationData
      };

      applicationInitiationData = this.formatApplicationInitiationDataRequest(
        applicationInitiationData
      );
      console.log(applicationInitiationData);

      let initiateApplication = InitiateNewApplication(
        applicationInitiationData
      );
      initiateApplication
        .then(response => {
          // Unique ID generated for the new IPO application
          const token = response.data.data.token;

          // Store the application with the generated UUID
          const newApplication = {
            ...applicationInitiationData,
            token,
            initiated: true,
            isInitiating: false
          };

          // Used to hide the initiate application button
          this.applicationInitiated = true;

          this.$store.commit(
            mutationTypes.UPDATE_APPLICATION_INITIATION_STATE,
            newApplication
          );

          this.initiatingApplication = false;
          this.showApplicationInitiatedPopup();
        })
        .catch(err => {
          console.log(err);

          this.initiatingApplication = false;
          this.applicationInitiated = false;
          this.$store.commit(
            mutationTypes.UPDATE_APPLICATION_INITIATION_STATE,
            { isInitiating: false }
          );
        });
    },

    /**
     * Submit an IPO Application
     */
    submitApplication() {
      // TODO Validate current form state needed to initiate the application
      this.submittingApplication = true;

      let applicationData = {
        ...this.$store.state.ipoApplication.applicationInitiationData,
        ...this.$store.state.ipoApplication.applicantData,
        ...this.$store.state.ipoApplication.signatures
      };

      applicationData = this.formatApplicationDataRequest(applicationData);
      console.log(applicationData);

      let submitApplication = SubmitApplication(applicationData);
      submitApplication
        .then(response => {
          console.log(response);
          // const applicationData = response.data.application;

          this.submittingApplication = false;
          this.showApplicationSubmittedPopup();
        })
        .catch(error => {
          this.submittingApplication = false;
          console.log(error);
        });
    },

    /**
     * Show the popup which indicates that an IPO application has been initiated.
     */
    showApplicationInitiatedPopup: function(code, event) {
      // Display the  popup modal/dialog
      document.querySelector("#openApplicationInitiatedDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      if (event) {
        event.stopPropagation();
      }
    },

    /**
     * Close the popup signalling that an IPO application has been initiated.
     */
    closeApplicationInitiatedPopup() {
      this.showApplicationInitiatedDialog = false;
      this.changeActiveStepper(2);
    },

    /**
     * Change the currently visible stepper content
     */
    changeActiveStepper(stepperIndex) {
      this.applicantStepper = stepperIndex;
    },

    /**
     * Save an ongoing IPO application.
     * This enables users continue the application at a later date, with their data already pre-filled.
     */
    saveApplication() {
      const applicationInitiationRequest = this.formatApplicationInitiationDataRequest(
        {
          ...this.$store.state.ipoApplication.applicationInitiationData
        }
      );
      const applicationDataRequest = this.formatApplicationDataRequest({
        ...this.$store.state.ipoApplication.applicantData
      });

      const applicationData = {
        ...applicationInitiationRequest,
        ...applicationDataRequest
      };

      let saveApplication = SaveApplication(applicationData);
      console.log(applicationData);
      this.isSavingApplication = true;

      saveApplication
        .then(response => {
          console.log(response);
          this.isSavingApplication = false;
          this.applicationSaved = true;
        })
        .catch(err => {
          console.log(err);
          this.isSavingApplication = false;
          this.applicationSaveFailed = true;
        });
    },

    /**
     * Display the popup which indicates that an IPO application has been submitted.
     */
    showApplicationSubmittedPopup: function(code, event) {
      // Display the  popup modal/dialog
      document.querySelector("#openApplicationSubmittedDialog").click();

      /**
       * Stopping the event propagation because of the auto-close quirk that vuetify's dialog
       * popup has if the click event is not triggered from within the activator slot of the
       * dialog component
       */
      if (event) {
        event.stopPropagation();
      }
    },

    /**
     * Close the popup which indicates that an IPO application has been submitted.
     */
    closeApplicationSubmittedPopup() {
      this.showApplicationSubmittedDialog = false;
      this.changeActiveStepper(5);
    },

    /**
     * Format the application data into a format suitable for the API call
     */
    formatApplicationDataRequest(applicationData) {
      /**
       * Set the names, based on application type
       */

      // Individual applications
      if (applicationData.applicationType === 1) {
        applicationData.names = [
          {
            firstName: applicationData.firstName,
            lastName: applicationData.lastName,
            otherName: applicationData.otherName
          }
        ];

        // Signatures
        applicationData.appSignatures = [applicationData.signature_1];
      } else if (applicationData.applicationType === 2) {
        // Joint applications
        applicationData.names = [
          {
            firstName: applicationData.firstName1,
            lastName: applicationData.lastName1,
            otherName: applicationData.otherName1
          },
          {
            firstName: applicationData.firstName2,
            lastName: applicationData.lastName2,
            otherName: applicationData.otherName2
          }
        ];

        // Signatures
        applicationData.appSignatures = [
          applicationData.signature_1,
          applicationData.signature_2
        ];
      } else {
        // Corporate applications
        applicationData.names = [{}];

        // Signatures
        applicationData.appSignatures = [
          applicationData.signature_1,
          applicationData.signature_2
        ];
      }

      /**
       * Bids, no_of_shares and total_amount
       */
      applicationData.bids = [
        {
          bidPrice: applicationData.bidPrice_1,
          no_of_shares: applicationData.noOfShares_1,
          amount: applicationData.amount_1
        },
        {
          bidPrice: applicationData.bidPrice_2,
          no_of_shares: applicationData.noOfShares_2,
          amount: applicationData.amount_2
        },
        {
          bidPrice: applicationData.bidPrice_3,
          no_of_shares: applicationData.noOfShares_3,
          amount: applicationData.amount_3
        }
      ];

      return applicationData;
    },

    formatApplicationInitiationDataRequest(applicationInitiationData) {
      // Individual applications
      if (applicationInitiationData.applicationType === 1) {
        applicationInitiationData.names = [
          {
            firstName: applicationInitiationData.firstName,
            lastName: applicationInitiationData.lastName
          }
        ];
      } else if (applicationInitiationData.applicationType === 2) {
        // Joint applications
        applicationInitiationData.names = [
          {
            firstName: applicationInitiationData.firstName1,
            lastName: applicationInitiationData.lastName1,
            otherName: applicationInitiationData.otherName1
          },
          {
            firstName: applicationInitiationData.firstName2,
            lastName: applicationInitiationData.lastName2,
            otherName: applicationInitiationData.otherName2
          }
        ];
      } else {
        // Corporate applications
        applicationInitiationData.names = [{}];
      }

      return applicationInitiationData;
    }
  }
};
</script>

<style lang="sass" scoped>
</style>



