// All mutation types that can be carried out on the store state by the application
import * as mutationTypes from "../../../mutation-types.js";

const mutations = {
  [mutationTypes.UPDATE_APPLICATION_INITIATION_STATE](state, newDataObject) {
    state.applicationInitiationData = {
      ...state.applicationInitiationData,
      ...newDataObject
    };
  },

  [mutationTypes.UPDATE_APPLICANT_DATA_STATE](state, newDataObject) {
    state.applicantData = {
      ...newDataObject
    };
  },

  [mutationTypes.UPDATE_CURRENT_STEP_STATE](state, newStep) {
    state.currentStep = newStep;
  },

  [mutationTypes.UPDATE_SIGNATURE_ONE](state, signatureFormData) {
    state.signatures.signature_1 = signatureFormData;
  },

  [mutationTypes.UPDATE_SIGNATURE_TWO](state, signatureFormData) {
    state.signatures.signature_2 = signatureFormData;
  },

  [mutationTypes.STORE_APPLICATION_TOKEN](state, token) {
    state.token = token;
  },

  /**
   * Parse and convert data gotten from the API to a format accepted by the vuex store
   */
  [mutationTypes.STORE_EXISTING_APPLICATION_STATE](state, existingState) {
    console.log(existingState);

    const existingStateObject = {
      applicationInitiationData: {
        ipo_id: 1,
        isInitiating: false,
        initiated: false,
        applicationType: "",
        companyName: "",
        email: "",
        firstName: "",
        lastName: "",
        token: null,
        // otherName: "",
        firstName1: "",
        lastName1: "",
        otherName1: "",
        firstName2: "",
        lastName2: "",
        otherName2: "",
        initiatingApplication: false,
        inPreviewState: false
      },
      applicantData: {
        // Corporate
        contactPerson: "",

        // ALL
        nok_phoneNumber: "",
        nok_name: "",
        otherName: "",
        address_street: "",
        address_city: "",
        address_state: "",
        phoneNumber: "",
        noOfShares_1: "",
        bidPrice_1: "",
        amount_1: "",
        noOfShares_2: "",
        bidPrice_2: "",
        amount_2: "",
        noOfShares_3: "",
        bidPrice_3: "",
        amount_3: "",
        cscsNo: "",
        chnNo: "",
        broker: "",
        bankName: "",
        bankAccountNumber: "",
        bankAccountName: "",
        bvn: "",
        bankSortCode: "",
        bankCity: "",
        bankState: ""
      },
      signatures: {
        signature: null,
        signature_1: null,
        signature_2: null
      }
    };
  },

  [mutationTypes.STORE_STOCKBROKERS](state, stockbrokers) {
    state.stockbrokers = stockbrokers;
  }
};

export default mutations;



